import React from "react";
import { Popover, Button } from "antd";
import { DELETE_FOR_ME, DELETE_FOR_EVERYONE } from "../../headings";

const content = (props) => {
  let filteredConvoMessages = props.convoMessages;
  const dummyConvoMessages = [];
  let appMsg = props.checkedMessages.some((msg) => !msg?.is_responder_message);
  const msgByOtherUser = props.checkedMessages.some(
    (msg) => msg?.created_by !== props?.userID
  );
  const sortedCheckedMessages = props.hotline
    ? props.checkedMessages.sort(
        (a, b) =>
          a?.user_hotline_conversation_id - b?.user_hotline_conversation_id
      )
    : props.checkedMessages.sort(
        (a, b) =>
          a?.user_openline_conversation_id - b?.user_openline_conversation_id
      );
  const firstCheckedMessage = sortedCheckedMessages[0];
  if (firstCheckedMessage) {
    let key = 0;
    if (props.hotline) {
      for (let convo of props.convoMessages) {
        if (
          convo?.user_hotline_conversation_id?.toString() !==
          firstCheckedMessage?.user_hotline_conversation_id.toString()
        ) {
          key = props.convoMessages.indexOf(convo);
          dummyConvoMessages.push(convo);
        }
        if (
          convo?.user_hotline_conversation_id?.toString() ===
          firstCheckedMessage?.user_hotline_conversation_id.toString()
        ) {
          break;
        }
      }
      dummyConvoMessages.forEach((dummy) => {
        filteredConvoMessages = filteredConvoMessages.filter(
          (filter) =>
            filter.user_hotline_conversation_id !==
            dummy.user_hotline_conversation_id
        );
      });
    } else {
      for (let convo of props.convoMessages) {
        if (
          convo?.user_openline_conversation_id?.toString() !==
          firstCheckedMessage?.user_openline_conversation_id.toString()
        ) {
          key = props.convoMessages.indexOf(convo);
          dummyConvoMessages.push(convo);
        }
        if (
          convo?.user_openline_conversation_id?.toString() ===
          firstCheckedMessage?.user_openline_conversation_id.toString()
        ) {
          break;
        }
      }
      dummyConvoMessages.forEach((dummy) => {
        filteredConvoMessages = filteredConvoMessages.filter(
          (filter) =>
            filter.user_openline_conversation_id !==
            dummy.user_openline_conversation_id
        );
      });
    }
    filteredConvoMessages.forEach((filter) => {
      if (!filter.is_responder_message) {
        appMsg = true;
      }
    });
  }

  return (
    <div>
      {/* check if all the selected messages inside the conversation are from the web user or not */}
      {appMsg ? (
        <ul>
          <li>
            <>
              <Button
                onClick={() =>
                  props.deleteMessage("deleteForMe", props.messagesIds)
                }
                disabled={props.messagesIds.length === 0}
              >
                {DELETE_FOR_ME}
              </Button>
            </>
          </li>
        </ul>
      ) : /* check if all the selected messages are from the logged in web user */
      !msgByOtherUser ? (
        <ul>
          <li>
            <>
              <Button
                onClick={() =>
                  props.deleteMessage("deleteForMe", props.messagesIds)
                }
                disabled={props.messagesIds.length === 0}
              >
                {DELETE_FOR_ME}
              </Button>
            </>
          </li>
          {/* check if all the selected messages inside the conversation are from the web user and their sent time is not beyond 8 minutes */}
          {Object.keys(props.timeExpired).length > 0 &&
          !Object.values(props.timeExpired).includes(true) ? (
            <li>
              <>
                <Button
                  onClick={() =>
                    props.deleteMessage("deleteForEveryone", props.messagesIds)
                  }
                  disabled={props.messagesIds.length === 0}
                >
                  {DELETE_FOR_EVERYONE}
                </Button>
              </>
            </li>
          ) : null}
        </ul>
      ) : (
        <>
          <Button
            onClick={() =>
              props.deleteMessage("deleteForMe", props.messagesIds)
            }
            disabled={props.messagesIds.length === 0}
          >
            {DELETE_FOR_ME}
          </Button>
        </>
      )}
    </div>
  );
};

class DeleteMessagePopover extends React.Component {
  state = {
    visible: false,
  };

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (nextProps.deleteMessagePopup) {
      this.hide();
    }
  }

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
    this.props.setHideDeleteMessagePopup(false);
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={content(this.props)}
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span id="deleteConversation" className="icon icon-delete" />
        </Popover>
      </>
    );
  }
}

export default DeleteMessagePopover;
